import { Col, Row, Popover } from 'antd';
import React from 'react';
//icons import
import {
  PlusOutlined,
  PlusCircleOutlined,
  ExclamationCircleTwoTone,
  QuestionCircleTwoTone,
} from '@ant-design/icons';
import BlogCard from '../../UiComponents/ProfilePage/BlogCard';
const content = (
  <div className='popover-message'>
    <p>
      When you upload a blog, It'll first get verified by the admin, after the
      verification the blog will apear in profile. The verification may take
      longer time. For any queries you can mail at{' '}
      <a
        href='mailto:mentor.ginnovation@gmail.com'
        target='_blank'
        rel='noreferrer'
      >
        mentor.ginnovation@gmail.com
      </a>
    </p>
  </div>
);
const ProfileBlog = (props) => {
  const blogs = props.data;
  const pendingBlogs =
    blogs &&
    blogs.filter((value) => {
      return value.status === 'pending';
    });
  const approvedBlogs =
    blogs &&
    blogs.filter((value) => {
      return value.status === 'approved';
    });
  return (
    <section className='blogs'>
      <div className='container'>
        {blogs && blogs.length !== 0 ? (
          <>
            <h4 className='section-head'>Blogs by {props.username}</h4>
            <Row gutter={12} className='blog-container'>
              {approvedBlogs.map((value, index) => {
                return (
                  <Col md={8} key={index}>
                    <BlogCard value={value} refresh={props.refresh} />
                  </Col>
                );
              })}
            </Row>
            {pendingBlogs && pendingBlogs.length !== 0 && (
              <div className='pending-status'>
                <p>
                  <ExclamationCircleTwoTone className='icon' />
                  You have {pendingBlogs.length} pending blogs. Once it is
                  approved by the admin, It will reflect on your profile
                </p>
                <Popover content={content} title='Pending Blogs'>
                  <div className='more'>
                    Know More <QuestionCircleTwoTone className='icon' />
                  </div>
                </Popover>
              </div>
            )}
            {props.editable && (
              <>
                <div className='edit-btn type2' onClick={props.addBlog}>
                  <PlusOutlined />
                </div>
              </>
            )}
          </>
        ) : (
          <div className='fresh-section' onClick={props.addBlog}>
            <header>
              <PlusCircleOutlined className='fresh-icon' />
              <p>Add Your Blogs</p>
              <p>Share your amazing thoughts by writing down a fresh Blog</p>
            </header>
          </div>
        )}
      </div>
    </section>
  );
};

export default ProfileBlog;
