import React from 'react';
import {
  HomeOutlined,
  MailOutlined,
  FieldTimeOutlined,
} from '@ant-design/icons';
import review from '../../../assets/review.svg';
import survey from '../../../assets/survey.svg';
const StatusPending = () => {
  return (
    <div className='status status-pending container'>
      <div className='message'>
        <img src={survey} alt='under review profile' />
        <h2>Under Review</h2>
        <p>
          Profile is under review! It will reflect here as soon as it gets
          verified by Ginnovation Admin.
        </p>
        <p>
          <FieldTimeOutlined className='icon' />
          Expected time period is 4 working days
        </p>
        <div className='btn-group'>
          <button>
            <HomeOutlined className='icon' />
            Back Home
          </button>
          <a href='mailto:admin@ginnovation.club'>
            <button>
              <MailOutlined className='icon' />
              Mail Admin
            </button>
          </a>
        </div>
      </div>
      <img src={review} alt='ginnovation under review profile' />
    </div>
  );
};

export default StatusPending;
