import React, { useState } from 'react';
//antd imports
import { Form, Input, Button, Row, Col, Spin, message, Popconfirm } from 'antd';
import { FormOutlined, CameraOutlined } from '@ant-design/icons';
import { EditOutlined, CloseOutlined } from '@ant-design/icons';
import axios from 'axios';
//blog
import { CKEditor } from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
//
const EditBlog = (props) => {
  const [dataChanged, setDataChanged] = useState(false);
  const [editorContent, seteditorContent] = useState('Blog content');
  const [spin, setSpin] = useState(false);
  const [form] = Form.useForm();
  const token = localStorage.getItem('token');
  const data = props.value;

  const onFinish = (values) => {
    setDataChanged(true);
    const updatedData = {
      blogTitle: values.blogTitle,
      coverpic: values.coverpic,
      email: data.email,
      creatorsID: data.creatorsID,
      subTitle: values.subTitle,
      tags: ['hi', 'hi'],
      blogContent: editorContent,
      status: 'pending',
    };
    setSpin(true);
    axios
      .put(
        `https://ginnovation-server.herokuapp.com/api/blog/${data._id}`,
        updatedData,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      )
      .then((resp) => {
        setSpin(false);
        if (resp.data.status === 'success') {
          message.success('Successfully Edited Data');
          props.handleClose(true);
          props.refresh();
        } else {
          message.error('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        message.error('An error occured!');
      });
  };

  const deleteBlog = () => {
    setSpin(true);
    axios
      .delete(`https://ginnovation-server.herokuapp.com/api/blog/${data._id}`, {
        headers: { Authorization: `Bearer ${token}` },
      })
      .then((resp) => {
        setSpin(false);
        if (resp.data.status === 'success') {
          message.success('Successfully Deleted Data');
          props.handleClose(true);
          props.refresh();
        } else {
          message.error('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        message.error('An error occured!');
      });
  };

  return (
    <div className='modal-container'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='edit-modal edit-blog'>
        <h4>
          Edit Blog <EditOutlined className='icon' />
          <CloseOutlined
            className='close-btn'
            onClick={() => {
              props.handleClose(dataChanged);
            }}
          />
        </h4>
        <Form
          form={form}
          name='addBlog'
          onFinish={onFinish}
          initialValues={props.value}
        >
          <Form.Item
            name='blogTitle'
            rules={[
              {
                required: true,
                message: 'Please input the title!',
              },
            ]}
          >
            <Input
              prefix={<FormOutlined className='site-form-item-icon' />}
              placeholder='Blog Title'
              showCount
              maxLength={50}
            />
          </Form.Item>
          <Row gutter={5}>
            <Col md={16}>
              <Form.Item name='subTitle'>
                <Input
                  prefix={<FormOutlined className='site-form-item-icon' />}
                  placeholder='Blog Sub-Title'
                />
              </Form.Item>
            </Col>
            <Col md={8}>
              <Form.Item name='coverpic'>
                <Input
                  prefix={<CameraOutlined className='site-form-item-icon' />}
                  placeholder='Cover Image Link'
                />
              </Form.Item>
            </Col>
          </Row>
          <div className='editor-wrapper'>
            <CKEditor
              editor={ClassicEditor}
              data={props.value.blogContent}
              onChange={(event, editor) => {
                const data = editor.getData();
                console.log({ event, editor, data });
                seteditorContent(data);
              }}
              config={{
                removePlugins: [
                  'Image',
                  'ImageCaption',
                  'ImageStyle',
                  'ImageToolbar',
                  'ImageUpload',
                  'MediaEmbed',
                  'Table',
                ],
              }}
            />
          </div>
          <div className='form-btn-row'>
            <div className='register-btn-container btn-container mt-3'>
              <Button
                type='secondary'
                className='mr-2'
                onClick={props.handleClose}
              >
                Cancel
              </Button>
              <Popconfirm
                placement='bottomRight'
                title='Are you sure to delete this task?'
                onConfirm={deleteBlog}
              >
                <Button type='danger' className='mr-2'>
                  Delete
                </Button>
              </Popconfirm>

              <Button type='primary' htmlType='submit'>
                Save Changes
              </Button>
            </div>
          </div>
        </Form>
      </div>
    </div>
  );
};

export default EditBlog;
