import React from 'react';
import {
  ExclamationCircleTwoTone,
  PlusCircleOutlined,
  PlusOutlined,
  QuestionCircleTwoTone,
} from '@ant-design/icons';
import FeaturedVideoSwiper from '../../UiComponents/HomePage/FeaturedVideoSwiper';
import ProjectCard from '../../UiComponents/ProfilePage/ProjectCard';
import { Col, Row, Popover } from 'antd';
const content = (
  <div className='popover-message'>
    <p>
      When you upload a project, It'll first get verified by the admin, after
      the verification the project will apear in profile. The verification may
      take longer time. For any queries you can mail at{' '}
      <a
        href='mailto:mentor.ginnovation@gmail.com'
        target='_blank'
        rel='noreferrer'
      >
        mentor.ginnovation@gmail.com
      </a>
    </p>
  </div>
);
const ProfileProject = (props) => {
  const projects = props.data;
  const pendingProjects =
    projects &&
    projects.filter((value) => {
      return value.status === 'pending';
    });
  const approvedProjects =
    projects &&
    projects.filter((value) => {
      return value.status === 'approved';
    });
  console.log(projects);
  console.log('penidng projects:', pendingProjects);
  const cards =
    approvedProjects &&
    approvedProjects.map((value, index) => {
      return (
        <Col md={8} key={index}>
          <ProjectCard value={value} editProject={props.editProject} editable />
        </Col>
      );
    });
  return (
    <section className='projects'>
      <div className='container'>
        {projects && projects.length !== 0 ? (
          <>
            <h4 className='section-head'>Projects by {props.username}</h4>
            {approvedProjects.length > 3 ? (
              <FeaturedVideoSwiper
                data={approvedProjects}
                editProject={props.editProject}
              />
            ) : (
              <Row gutter={12}>{cards}</Row>
            )}
            {props.editable && (
              <>
                <div className='edit-btn type2'>
                  <PlusOutlined onClick={props.addProject} />
                </div>
              </>
            )}
            {pendingProjects && pendingProjects.length !== 0 && (
              <div className='pending-status'>
                <p>
                  <ExclamationCircleTwoTone className='icon' />
                  You have {pendingProjects.length} pending project. Once it is
                  approved by the admin, It will reflect on your profile
                </p>
                <Popover content={content} title='Pending Projects'>
                  <div className='more'>
                    Know More <QuestionCircleTwoTone className='icon' />
                  </div>
                </Popover>
              </div>
            )}
          </>
        ) : (
          <div className='fresh-section' onClick={props.addProject}>
            <header>
              <PlusCircleOutlined className='fresh-icon' />
              <p>Add Your Projects</p>
              <p>
                Remember, The more projects you will do, the more you will learn
              </p>
            </header>
          </div>
        )}
      </div>
    </section>
  );
};

export default ProfileProject;
